@font-face {
  font-family: 'dejavu';
  src: url("../dejavu-sans/DejaVuSans.ttf");
}

* {
  font-family: "dejavu";
  box-sizing: border-box;
}

body {
  margin: 0;
}

.container {
  max-width: 1140px;
  margin: 0 auto;
}

.hide {
  visibility: hidden;
  transition: .4s all;
  opacity: 0;
  position: relative;
  right: -80px;
}

.show {
  visibility: visible;
  transition: .6s all;
  opacity: 1;
  right: 0;
}

.page-box__top-bar {
  height: 138px;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.page-box__logo {
  margin: 0;
  height: 100%;
}

.page-box__logo--link {
  display: flex;
  align-items: center;
  height: 100%;
}

.page-box__company {
  height: 100%;
  display: flex;
  align-items: center;
}

.page-box__company__logo {
  margin-right: 30px;
}

.page-box__company__logo--link {
  color: #00369a;
  text-decoration: none;
  transition: 1s all;
}

.page-box__company__logo--link:hover {
  color: red;
}

.page-box__company__advert {
  background-color: #00369a;
  height: 100px;
  width: 110px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  color: #fff;
  font-size: 10px;
  padding: 5px;
}

.page-box__hero {
  background-color: #f3f3f3;
  height: 425px;
  background-image: url("../images/hero.png");
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;
}

.page-box__hero--container {
  display: flex;
  height: 100%;
  align-items: center;
}

.page-box__rectangle {
  width: 210px;
  height: 250px;
  background: #ec3123;
  background: -moz-linear-gradient(-45deg, #ec3123 0%, #ec3123 49%, #8a231d 100%);
  background: -webkit-gradient(left top, right bottom, color-stop(0%, #ec3123), color-stop(49%, #ec3123), color-stop(100%, #8a231d));
  background: -webkit-linear-gradient(-45deg, #ec3123 0%, #ec3123 49%, #8a231d 100%);
  background: -o-linear-gradient(-45deg, #ec3123 0%, #ec3123 49%, #8a231d 100%);
  background: -ms-linear-gradient(-45deg, #ec3123 0%, #ec3123 49%, #8a231d 100%);
  background: linear-gradient(135deg, #ec3123 0%, #ec3123 49%, #8a231d 100%);
  filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#ec3123', endColorstr='#8a231d', GradientType=1 );
  position: relative;
}

.page-box__rectangle::after {
  content: "";
  width: 210px;
  height: 40px;
  position: absolute;
  bottom: 0;
  background: #d4cab9;
  border-top-right-radius: 50px;
}

.page-box__rectangle--text {
  color: #fff;
  font-size: 32px;
  display: block;
  padding: 20px;
}

.page-box__information {
  width: 465px;
  margin-left: 30px;
  color: #fff;
}

.page-box__information--text {
  font-size: 15px;
  height: 110px;
  width: 465px;
}

.page-box__btn {
  width: 210px;
  height: 30px;
  margin-top: 15px;
  background: #fff;
  border: transparent;
  color: red;
  /* This code below was design by Andrew Spencer
   His codepen: https://codepen.io/iam_aspencer/pen/xXQbvK */
  transition: box-shadow 600ms cubic-bezier(0.33, 0.11, 0.02, 0.99), transform 600ms cubic-bezier(0.33, 0.11, 0.02, 0.99);
}

.page-box__btn:hover {
  box-shadow: 0 4px 4px rgba(0, 0, 0, 0.1), 0 1px 6px rgba(0, 0, 0, 0.05), 0 8px 8px rgba(0, 0, 0, 0.1), 0 16px 16px rgba(0, 0, 0, 0.1), 8px 32px 32px rgba(0, 0, 0, 0.15), 8px 64px 64px rgba(0, 0, 0, 0.15);
  transform: scale(1.05) translateY(-0.5rem);
}

.page-box__btn:active {
  box-shadow: 0 4px 4px rgba(0, 0, 0, 0.1), 0 1px 6px rgba(0, 0, 0, 0.05), 0 8px 8px rgba(0, 0, 0, 0.1), 0 16px 16px rgba(0, 0, 0, 0.1), 8px 16px 16px rgba(0, 0, 0, 0.15), 8px 32px 32px rgba(0, 0, 0, 0.15);
}

.page-box__diagram {
  height: 640px;
  display: flex;
  align-items: center;
  flex-direction: column;
}

.page-box__diagram__text {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  text-align: center;
  padding-bottom: 40px;
  width: 865px;
  height: 220px;
}

.diagram__text--header {
  margin-top: 40px;
}

.page-box__diagram__chart {
  display: flex;
  flex-wrap: wrap;
  width: 100%;
  justify-content: center;
}

.chart__left,
.chart__right {
  display: flex;
  width: 48%;
  height: 200px;
  background: #ebebeb;
}

.chart__left {
  margin-right: 30px;
}

.diagram__chart--icon {
  height: 100%;
  flex-grow: 4;
  display: flex;
  justify-content: center;
  align-items: center;
}

.diagram__chart--list {
  flex-grow: 6;
  color: #545454;
}

.chart--header {
  padding-left: 20px;
  margin-bottom: 0;
}

.chart--list {
  list-style: none;
  padding: 0;
}

.chart--item {
  padding: 5px 0;
  font-size: 20px;
}

.chart--item::before {
  content: "+";
  margin-right: 4px;
}

.chart__pipe {
  width: 580px;
  height: 120px;
  border: 15px solid #b1b1b1;
  border-top: transparent;
  display: flex;
  justify-content: center;
}

.chart__pipe--box {
  width: 360px;
  height: 130px;
  background: #b1b1b1;
  position: relative;
  bottom: -40px;
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  text-align: center;
  color: #fff;
}

.chart__pipe--box--header {
  width: 100%;
  margin-bottom: 0;
  font-size: 30px;
}

.chart__pipe--box--text {
  font-size: 24px;
}
